.EmojiItem {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  border-bottom: 1px solid #313131;
  padding: 1rem 0;
}

.EmojiItem--loading {
  opacity: 0.6;
  pointer-events: none;
}

.EmojiItem:last-child {
  border: none;
}

.EmojiItem-icon,
.EmojiItem-image {
  -ms-flex: 0 1 2rem;
      flex: 0 1 2rem;
  height: 2rem;
  width: 2rem;
}

.EmojiItem-icon {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.EmojiItem-name-error,
.EmojiItem-icon--error {
  color: #EC6363;
}

.EmojiItem-name {
  -ms-flex: 1 1;
      flex: 1 1;
  padding: 0.625rem;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
}

.EmojiItem-action {
  cursor: pointer;
  padding: 0 0.5rem;
}

.EmojiItem-action,
.EmojiItem-action--download:visited,
.EmojiItem-action--remove:visited {
  color: inherit;
}

.EmojiItem-action--download:hover {
  color: #72AAAA;
}

.EmojiItem-action--remove:hover {
  color: #EC6363;
}



